const routesList = [
	{path :  '/',  name: '', component: Main, meta: {title: 'Main'}}
];

const router = new VueRouter({
	mode: 'history',
	routes: routesList,
	scrollBehavior: function(to, from, savedPosition) {
		return {
			x: 0,
			y: 0
		}
	}
});

router.beforeEach((to, from, next) => {
	document.title = to.meta.title;
	next()
});

new Vue({
	el: "#app",
	router: router
	// $store: $store

});

